import React, { Component } from 'react';
import './Home.css';
import GlobalNavigation from '../../../src/components/GlobalNavigation/GlobalNavigation';

class Home extends Component {

  render() {
    return (
      <div>
        <h1>Ethers V5 调用合约示例</h1>
        <GlobalNavigation history={this.props.history} />
               
        <div className="css_addr">
            <div className="information">
                <ul>
                <span className="font_bold" >使用说明：</span><br />
                  <li>演示创建组织的投票，不包含所有合约的所有方法</li>
                  <li>区块链网络：Polygon测试网 Mumbai</li>
                  <li>合约管理员地址：【0xb9b42298EE1c2960336Ac45BB30294f1F2568fBE，0xc9aBec4d26aF7507D351379aC51907D6ebb80A2】</li>
                </ul>
                <ul>
                <span className="font_bold" >前提条件：</span><br />
                  <li>科学上网</li>
                  <li>开启后端监听创建投票事件服务</li>
                </ul>
                <ul>
                <span className="font_bold" >步骤：</span><br />
                  <li className='font_bold'>1、连接钱包</li>
                  <li>1.1、点击页面右上方"连接钱包"按钮 - 调用MetaMask连接钱包， 连接钱包</li>
                  <li className='font_bold'>2、投票工厂合约 </li>
                  <li>2.1、创建投票 - 更改投票信息，选中投票成员，点击 "创建投票（创建组织）"按钮 - 创建成功会显示投票合约地址</li>
                  <li className='font_bold'>3、Token合约</li>
                  <li>3.1、发行Token - 填写Token数量及账户地址，此方法拥有MINT权限的管理员调用</li>
                  <li className='font_bold'>4、投票合约</li>
                  <li>4.1、投票 - 输入创建投票时生成的投票合约地址，参与人点击"投票"按钮 （为赞成票）</li>
                  <li>4.2、查看投票结果 - 输入创建投票时生成的投票合约地址，点击"查看投票结果"按钮 </li>
                  <li className='font_bold'>5、组织合约</li>
                  <li>5.1、邀请成员加入组织 - 输入组织合约地址及邀请成员地址，点击"邀请成员加入组织"按钮</li>
                  <li>5.2、同意加入组织 - 连接钱包为要加入组织的成员 - 输入组织合约地址，点击"同意加入组织"按钮</li>
                  <li>5.3、移除组织成员 - 输入组织合约地址及要移除的成员地址，点击"移除组织成员"按钮</li>
                  <li>5.4、查看组织成员 - 输入组织合约地址，点击"查看组织成员"按钮</li>
                </ul>
            </div>
        </div>
      </div>
    );
  }
}

export default Home;
