import React, { Component } from 'react';
import ConnectWallet from '../../../src/components/ConnectWallet/ConnectWallet';

class GlobalNavigation extends Component {
  handleClick = (path) => {
    this.props.history.push(path);
  };

  render() {
    return (
      <div>
        <div className="button-container">
          <button onClick={() => this.handleClick('/')}>使用说明</button>
          <button onClick={() => this.handleClick('/BallotFactory')}>投票工厂合约</button>
          <button onClick={() => this.handleClick('/Token')}>Token合约</button>
          <button onClick={() => this.handleClick('/Ballot')}> 投票合约</button>
          <button onClick={() => this.handleClick('/Organization')}> 组织合约</button>
          <ConnectWallet />
        </div>
      </div>
    );
  }
}

export default GlobalNavigation;
