import React,{ Component } from "react";
import { ethers } from "ethers";
import GlobalNavigation from "../../../src/components/GlobalNavigation/GlobalNavigation";
import { connect } from "react-redux";
import "./BallotFactory.css";

// Token合约地址
const TokenAddr = "0x6e738dB95edF1E0760da14fCeE2294E4Cebf70b9";
// 组织工厂合约地址
const OrgFactoryAddr = "0x105cDb2F9b744B59690BE6530d2E81511E6013CD";
// 根组织合约地址
const RootOrgAddr = "0x9bEB487086654361509ec9f08887D04dC01E59C4";
// 投票工厂合约地址
const BallotFactoryContractAddr = "0x09a91A6044f91c8971c30657eDB2CC8b33f62d74";
// 治理激励合约地址
const ExcitationAddr = "0x3A4A943991A29cfB2a86cDE05fDFFB15896c479e";

class BallotFactory extends Component {
    constructor() {
        super();
        this.state = {
            address: "",
            signer: null,
            ballotFactoryContract: null,
            adminAddr:"",
            ballotAddr: "",
            ballotId: "001",
            stakedTokens: 10, //投票所需的token数量
            topic: "创建组织的投票", //投票主题
            passPercent: 100, //投票通过的百分比
            orgAddr: RootOrgAddr, //默认根组织合约地址
            deadline_min: 30,  //投票截止时间（分钟）
            rules: "规章制度", //规章制度
            members: [],
            checkMemberAddrs: [],
        };
    }

    saveFormData = (event) => {
        const { name, value } = event.target;
        this.setState({
            [name]: value,
        });
    };

    saveFormCheckbox = (event) => {
        const { name, value } = event.target;
        this.setState(prevState => {
            const checkMemberAddrs = [...prevState.checkMemberAddrs];
            const index = checkMemberAddrs.indexOf(value);
            if (index !== -1) {
                checkMemberAddrs.splice(index, 1);
            } else {
                checkMemberAddrs.push(value);
            }
            return { checkMemberAddrs };
        }, () => {
            console.log("checkMemberAddrs: ", this.state.checkMemberAddrs);
        });
    };

    // 创建投票工厂合约对象
    init = async () => {
        const { address, signer } = this.props;
        //合约ABI
        const response = require("../../constants/abi/BallotFactory.json");
        const abi = response.abi;
        const ballotFactoryContract = new ethers.Contract(BallotFactoryContractAddr, abi, signer); 
        this.setState({address: address, signer:signer, ballotFactoryContract: ballotFactoryContract});
    }

    // 获取投票工厂合约管理员地址
    getOwner = async () => {
        await this.init();
        let owner = await this.state.ballotFactoryContract.owner();
        this.setState({adminAddr: owner});
    }

    // 创建投票
    createBallot = async () => {
        await this.init();

        //调用合约方法（交易）- 创建投票合约
        console.log("-----------创建投票-----------");
        console.log("checkMemberAddrs: ", this.state.checkMemberAddrs);
         //当前时间
        const now = new Date();
        //过期时间
        const deadline = parseInt((now.setMinutes(now.getMinutes() + parseInt(this.state.deadline_min))) / 1000);

        const topic = ethers.utils.keccak256(ethers.utils.toUtf8Bytes(this.state.topic));

        let ballotParamInfo = {
            id: this.state.ballotId,
            stakedTokens: this.state.stakedTokens,
            topic: topic,
            deadline: deadline,
            passPercent: this.state.passPercent,
            tokenAddr: TokenAddr,
            excitationAddr: ExcitationAddr,
            voters: this.state.checkMemberAddrs,
            calldataContracts: [],
            calldataValues: [],
        }
        console.log("-------生成创建组织的指令集 -------");

        //指令集 - 函数参数
        const orgAId = "001";  //组织ID
        const rules = this.state.rules;  //章程

        //指令集 - Solidity中函数的名称和参数类型
        const functionName = "createOrg";
        const functionParamType = ["string", "string",  "address", "address"];

        //指令集 - 参数类型及参数值 
        const paramType = ["string", "string", "address", "address"];;
        const paramVal = [orgAId, rules, this.state.orgAddr, this.state.address];

        //生成指令集
        // 用于编码函数名称和参数类型的selector
        const selector = ethers.utils.hexDataSlice(ethers.utils.keccak256(ethers.utils.toUtf8Bytes(`${functionName}(${functionParamType.join(',')})`)), 0, 4);

        //用于编码函数参数的ABI编码器
        const encodedData = ethers.utils.defaultAbiCoder.encode(
            paramType,
            paramVal
        );

        //构造包含函数选择器的payload
        const payload = selector + encodedData.substr(2); // 去除前面的'0x'

        //修改指令集
        ballotParamInfo.calldataContracts = [OrgFactoryAddr]
        ballotParamInfo.calldataValues = [payload]

        console.log("ballotParamInfo: ", ballotParamInfo);

        //创建投票合约（部署投票合约）
        console.log("-------创建投票合约-------"); 

        const options = {
            gasLimit: 7000000, // 可以设置为您需要的任何数量
        }

        // 调用投票工厂合约中创建投票方法
        const txResponse = await this.state.ballotFactoryContract.connect(this.state.signer).createBallot(this.state.orgAddr, this.state.address, ballotParamInfo, options);
        console.log("txResponse: ", txResponse);
        const txReceipt = await txResponse.wait(); 
        console.log("txReceipt: ", txReceipt);

        //查看事件中的投票合约地址
        const ballotAddr = txReceipt.events[0].args._ballotAddr;
        this.setState({ballotAddr: ballotAddr});
        console.log("投票合约地址：", ballotAddr);
    }

    componentDidMount = async () => {    
        await this.init();     

        // 组织合约对象
        const orgRes = require("../../constants/abi/Organization.json");
        const orgAbi = orgRes.abi;
        const orgObj = new ethers.Contract(this.state.orgAddr, orgAbi, this.state.signer);

        const members0 = await orgObj.getOrgMembers();
        console.log("members: ", members0.length, members0);
        let membersTmp = [];
        for (let i = 0; i < members0.length; i++) {
            let memberInfo = await orgObj.getMemberInfo(members0[i]);
            console.log("memberInfo: ", i, members0[i], memberInfo);
            if (memberInfo.agreeJoinOrg == 1) {
                membersTmp.push(members0[i]);
            }
            console.log("memberInfo111: ", i, members0[i], membersTmp);
        }
        this.setState({members:membersTmp});
        console.log("result members: ", this.state.members, membersTmp );
    }
    
    render() {
        console.log("render members: ", this.state.members);
        return (
            <div>               
                <h1>投票工厂合约 </h1>
                <GlobalNavigation history={this.props.history} />
                <div className="ballotFactory-container">
                <div class="input-container">
                    <label><span className="font_red">*</span> 投票ID：</label>
                    <input
                        className="input_text"
                        type="text"
                        name="ballotId"
                        value={this.state.ballotId}
                        onChange={this.saveFormData}
                    />
                </div>
                <div class="input-container">
                    <label><span className="font_red">*</span> 准入Token数量：</label>
                    <input
                        className="input_text"
                        type="text"
                        name="stakedTokens"
                        value={this.state.stakedTokens}
                        onChange={this.saveFormData}
                    />
                </div>
                <div class="input-container">
                    <label><span className="font_red">*</span> 投票名称：</label>
                    <input
                        className="input_text"
                        type="text"
                        name="topic"
                        value={this.state.topic}
                        onChange={this.saveFormData}
                    />
                </div>
                <div class="input-container">
                    <label><span className="font_red">*</span> 投票通过百分比：</label>
                    <input
                        className="input_text"
                        type="text"
                        name="passPercent"
                        value={this.state.passPercent}
                        onChange={this.saveFormData}
                    />
                </div>               
                <div class="input-container">
                    <label><span className="font_red">*</span> 过期时间(单位：分钟)：</label>
                    <input
                        className="input_text"
                        type="text"
                        name="deadline_min"
                        value={this.state.deadline_min}
                        onChange={this.saveFormData}
                    />
                </div>
                <div class="input-container">
                    <label><span className="font_red">*</span> 规章制度：</label>
                    <input
                        className="input_text"
                        type="text"
                        name="rules"
                        value={this.state.rules}
                        onChange={this.saveFormData}
                    />
                </div>
                <div class="input-container">
                    <label><span className="font_red">*</span> 组织合约地址：</label>
                    <input
                        className="input_text"
                        type="text"
                        name="orgAddr"
                        value={this.state.orgAddr}
                        onChange={this.saveFormData}
                    />
                </div>
                <div className="input-container">
                    
                    <label><span className="font_red">*</span> 选择投票成员：</label>
                    <br />
                    {this.state.members.map(item => (
                    <label key={item}>
                        <input 
                            type="checkbox" 
                            name="checkMemberAddrs" 
                            value={item}
                            onChange={this.saveFormCheckbox}
                        /> 
                        {item}
                    </label>
                    ))}
                    </div>
                <div>
                    <button onClick={this.createBallot}>创建投票（创建组织）</button>
                    <div className="css_addr">
                        {this.state.ballotAddr == "" ? "" : "投票合约地址：" + this.state.ballotAddr}
                    </div>
                    </div>
                    <br/> 
                </div>
            </div>
        )

    }
}

const mapStateToProps = (state) => ({
    connected: state.connected,
    address: state.address,
    signer: state.signer,
  });
  
export default connect(mapStateToProps)(BallotFactory);
